
import { Pagination, TextField } from "@mui/material";
import { Header, ChipLarge, CardViewImage, Footer, ListViewTitle } from "../components";
import { makeStyles } from "@mui/styles";
import { ChangeEvent, useState } from "react";
import { NavigateFunction, useNavigate, useParams } from "react-router-dom";

interface HomeProps {

}

const DefaultSearch: React.FC<HomeProps> = (props) => {
    const parm = useParams();
    console.log(parm);
    const clickEvnet = () => {
        console.info('You clicked the Chip.');
    };
    const linkGroupInfo = () => {
        window.location.href = "/groupinfo/1";
    };
    const useStyles = makeStyles((theme) => ({
        root: {
            '& .Mui-selected': {
                backgroundColor: '#573300 !important',
                color: 'white'
            }
        }
    }));

    const classes = useStyles();

    const boardList = [
        { id: 0, text: 'board 0' },
        { id: 1, text: 'board 1' },
        { id: 2, text: 'board 2' },
        { id: 3, text: 'board 3' },
        { id: 4, text: 'board 3' },
        { id: 5, text: 'board 3' },
        { id: 6, text: 'board 3' }
    ]
    const [posts, setPosts] = useState(boardList);
    const [currentPage, setCurrentPage] = useState(1);
    const [postsPerPage] = useState(6);
    const handleChange = (event: ChangeEvent<unknown>, value: number) => {
        setCurrentPage(() => value);
    };
    const indexOfLastPost = currentPage * postsPerPage;
    const indexOfFirstPost = indexOfLastPost - postsPerPage;
    const currentPosts = posts.slice(indexOfFirstPost, indexOfLastPost);
    const totalPage = Math.ceil(posts.length / postsPerPage);

    return (

        <div className="common-bg">
            <Header
                className=""
                goodroad="/asset/img/main_top_logo.svg"
                person="/asset/img/non_member_icon_man.svg"
                bell="/asset/img/main_Icon_awesome-bell.svg"
                kakao="/asset/img/main_icon_message.svg"
                color="font-bold-s12-c573300"
            />

            <div className="media-wrapper">

                {/* body */}
                <div className="common-wrapper__container"></div>
                {/* body container ---------- begin */}
                <div className="common-wrapper__container">
                    <div className="media-blank-60"></div>

                    {/* 상단 */}
                    <div className="common-flex__column">
                        <div className="media-common-row-wrap-hcenter-vstretch">
                            <img className="media-icon-quote" src="/asset/img/group_detail_icon_quote_left.svg" />
                            <span className="media-span-group-search">우리 아이에게 딱 맞는 <br />맟춤 그룹찾기</span>
                            <img className="media-icon-quote" src="/asset/img/group_detail_icon_quote_rignt.svg" />
                        </div>
                        <div className="common-flex-row-wrap-hcenter-vcenter">
                            <span className="media-span-good-load">GOOD ROAD</span>
                        </div>
                        <div className="common-flex-row-wrap-hcenter-vcenter" style={{ padding: 5 }}>
                            <div className="search">
                                <div className="search-input">
                                    <TextField
                                        style={{ width: '100%' }}
                                        name="text"
                                        size="small"
                                        id="outlined-error"
                                        label=""
                                        variant="standard"
                                        InputProps={{
                                            disableUnderline: true,
                                        }}
                                    />
                                </div>
                                <div className="search-button"><img src="/asset/img/main_icon_serch.svg" /></div>
                            </div>
                        </div>
                        <div className="common-flex-row-wrap-vcenter" style={{ marginTop: 10, paddingLeft: 5, paddingRight: 5 }}>
                            <ChipLarge width={200} type={1}>치료과목검색</ChipLarge>
                            <ChipLarge onClick={() => clickEvnet()}>언어치료</ChipLarge>
                            <ChipLarge>작업치료</ChipLarge>
                            <ChipLarge>감각통합</ChipLarge>
                            <ChipLarge>특수체육</ChipLarge>
                            <ChipLarge>상담</ChipLarge>
                            <ChipLarge>미술</ChipLarge>
                            <ChipLarge>응용행동분석(ABA)</ChipLarge>
                        </div>
                    </div>


                    {/* 그룹 - title */}
                    <div className="media-common-flex-row-wrap-space-between-vcenter"
                        style={{ marginBottom: 10, marginTop: 30 }}>
                        <ListViewTitle icon="/asset/img/new_member_icon_01.svg">검색결과</ListViewTitle>
                    </div>


                    {/* 그룹 - content */}
                    <div className="media-grid--col3__111">
                        {
                            currentPosts.map((item, index) => (
                                <div className="box-cgc" key={item.id}>
                                    <CardViewImage
                                        onClick={() => linkGroupInfo()}
                                        backgroundImage="url('/asset/img/main_img_groupimg_1.svg')" />
                                </div>
                            ))
                        }
                    </div>

                    <div className="common-flex-row-wrap-hcenter-vcenter" style={{ marginTop: 20 }}>

                        <Pagination count={totalPage}
                            variant="outlined" shape="rounded"
                            page={currentPage}
                            onChange={handleChange} />

                        {/* <Pagination activePage={page}
                            itemsCountPerPage={10} totalItemsCount={45}
                            pageRangeDisplayed={5} prevPageText={"‹"} nextPageText={"›"}
                            onChange={handlePageChange} /> */}

                    </div>
                    <div className="media-blank-bottom"></div>

                </div>
                {/* body container ---------- end */}
                <div className="common-wrapper__container"></div>

                <Footer></Footer>
            </div>
        </div>
    )
}


export default DefaultSearch


